import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';

import Main from './main.vue';
import Mobile from './Mobile.vue';

Vue.config.productionTip = false

Vue.use(VueRouter); // router   

const routes = [
    //  router    .
    {
      path: '/',
      component: Main
    },
    {
      path: '/index',
      component: Mobile
    },
];

// router 
const router = new VueRouter({ 
  routes,
  mode:"history"
});

new Vue({
  render: h => h(App),
  router // router 
}).$mount('#app')

